<template>
  <el-dialog
    title="添加规则"
    :close-on-click-modal="false"
    :visible.sync="dialogVisible"
    width="30%"
    :before-close="handleClose"
  >
    <el-input v-model="day1">
      <template slot="prepend">第一天</template>
      <template slot="append">蜗币</template>
    </el-input>
    <el-input v-model="day2">
      <template slot="prepend">第二天</template>
      <template slot="append">蜗币</template>
    </el-input>
    <el-input v-model="day3">
      <template slot="prepend">第三天</template>
      <template slot="append">蜗币</template>
    </el-input>
    <el-input v-model="day4">
      <template slot="prepend">第四天</template>
      <template slot="append">蜗币</template>
    </el-input>
    <el-input v-model="day5">
      <template slot="prepend">第五天</template>
      <template slot="append">蜗币</template>
    </el-input>
    <el-input v-model="day6">
      <template slot="prepend">第六天</template>
      <template slot="append">蜗币</template>
    </el-input>
    <el-input v-model="day7">
      <template slot="prepend">第七天</template>
      <template slot="append">蜗币</template>
    </el-input>
    <span slot="footer" class="dialog-footer">
      <el-button @click="handleClose">取 消</el-button>
      <el-button type="primary" @click="onSave">确 定</el-button>
    </span>
  </el-dialog>
</template>

<script>
export default {
  name: 'ruleAdd',

  props: {
    dialogVisible: {
      type: Boolean,
      default: false,
    }
  },

  data () {
    return {
      day1: '',
      day2: '',
      day3: '',
      day4: '',
      day5: '',
      day6: '',
      day7: '',
    }
  },
  
  methods: {
    handleClose () {
      this.$emit('close')
    },
    async onSave () {
      const data = {
        wohoCoinReward: `${this.day1},${this.day2},${this.day3},${this.day4},${this.day5},${this.day6},${this.day7}`
      }
      const res = await this.$http.post(this.$api.addRules, data)
      if (res.data.success) {
        this.$message.success('保存成功')
        this.handleClose()
        this.reload()
      } else {
        this.$message.error(res.data.msg)
      }
    }
  },

  inject: ['reload']
}
</script>

<style lang="less" scoped>

</style>
