<template>
  <div>
    <Layout-box one="规则列表">
      <el-button @click="onAdd">新增规则</el-button>
      <table-cmp :tableData="tableData" :tableColumn="tableColumn" @edit="onEdit" />
      <el-pagination class="mt-1 text-right" background :current-page="queryInit.pageNo" :page-sizes="[10, 20, 30, 40]" :page-size="queryInit.pageSize" layout="total, sizes, prev, pager, next, jumper" :total="queryInit.total" @size-change="handleSizeChange" @current-change="handleCurrentChange" />
    </Layout-box>

    <article>
      <rule-add-cmp :dialogVisible="dialogVisible" @close="onClose" />
      <rule-edit-cmp v-if="editVisible" :dialogVisible="editVisible" :selectData="selectData" @close="onEditClose" />
    </article>
  </div>
</template>

<script>
import LayoutBox from '@/components/LayoutBox'
import tableCmp from '@/components/table_cmp/index.vue'
import ruleAddCmp from './cmp/rule_add.vue'
import ruleEditCmp from './cmp/rule_edit.vue'

export default {
  name: 'rule',

  data () {
    return {
      queryInit: {
        pageNo: 1,
        pageSize: 10,
        total: 0,
      },
      tableData: [], // 规则列表
      tableColumn: [
        { label: '规则', prop: 'wohoCoinCeward', type: 'text' },
        { label: '操作', type: 'button', btnGroup: [{ value: 'edit', name: '修改' }] }
      ],

      // ------------------------
      dialogVisible: false,
      editVisible: false,
      selectData: {},
    }
  },
  
  components: {
    LayoutBox,
    tableCmp,
    ruleAddCmp,
    ruleEditCmp
  },

  async created () {
    await this.apiFindRules()
  },

  methods: {
    handleSizeChange (pageSize) {
      this.queryInit.pageSize = pageSize
    },
    handleCurrentChange (pageNo) {
      this.queryInit.pageNo = pageNo
    },
    // 点击新增规则
    onAdd () {
      if (this.queryInit.total > 0) return this.$message.error('只能添加一个规则')
      this.dialogVisible = true
    },
    // 添加规则窗口关闭
    onClose () {
      this.dialogVisible = false
    },
    // 编辑规则窗口关闭
    onEditClose () {
      this.editVisible = false
    },
    // 编辑规则
    onEdit (data) {
      this.selectData = data
      this.editVisible = true
    },

    // -----------------------------------------
    // 查询获取蜗币的规则列表
    async apiFindRules () {
      const data = {
        ...this.queryInit
      }
      const res = await this.$http.post(this.$api.findRules, data)
      this.tableData = res.data.data.list
      this.queryInit = res.data.data
    },
  }
}
</script>

