<template>
  <el-table
    :data="tableData"
    style="width: 100%"
    size="small"
  >
    <el-table-column
      v-for="(item, i) in tableColumn"
      :key="i"
      :label="item.label"
      :width="item.width"
    >
      <template slot-scope="scope">
        <div v-if="item.type === 'text'">
          <span v-if="item.formatter === 'shopZhuBoStatusFormatter'">{{ shopZhuBoStatusFormatter(scope) }}</span>
          <span v-else-if="item.formatter === 'shopZhuBoAnchorFormatter'">{{ shopZhuBoAnchorFormatter(scope) }}</span>
          <span v-else>{{ scope.row[item.prop] }}</span>
        </div>
        <div v-else-if="item.type === 'image'">
          <el-image fit="contain" :src="scope.row[item.prop]" />
        </div>
        <div v-else-if="item.type === 'button'">
          <div v-for="(btnGroupItem, btnGroupIndex) in item.btnGroup" :key="btnGroupIndex" style="display: inline-block">
            <el-button v-if="btnGroupItem.value === 'delete'" type="danger" size="small" style="margin: 0px 1px;" @click="onDelete(scope)">{{ btnGroupItem.name }}</el-button>
            <el-button v-if="btnGroupItem.value === 'edit'" type="success" size="small" style="margin: 0px 1px;" @click="$emit('edit', scope)">{{ btnGroupItem.name }}</el-button>
            <el-button v-if="btnGroupItem.value === 'shengHeListCustom'" type="primary" size="small" style="margin: 0px 1px;" @click="shengHeListCustom(scope)">{{ btnGroupItem.name }}</el-button>
          </div>
        </div>
      </template>
    </el-table-column>
  </el-table>
</template>

<script>
export default {
  name: 'table_cmp',

  props: {
    tableData: {
      type: Array,
      default: () => {
        return []
      }
    },
    tableColumn: {
      type: Array,
      default: () => {
        return []
      }
    }
  },

  methods: {
    shopZhuBoStatusFormatter (scope) {
      return scope.row.status === 1 ? '已通过' : scope.row.status === 2 ? '未通过' : '待审核'
    },
    shopZhuBoAnchorFormatter (scope) {
      return scope.row.anchor.realName
    },

    onDelete (scope) {
      this.$confirm('此操作将永久删除, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.$emit('delete', scope)
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        })        
      })
    },
    shengHeListCustom (scope) {
      if (scope.row.status !== 0) return this.$message.error('已审核')
      this.$prompt('请输入备注', '审核', {
        distinguishCancelAndClose: true,
        closeOnClickModal: false,
        confirmButtonText: '通过',
        cancelButtonText: '拒绝',
      }).then(({ action }) => {
        if (action === 'confirm') {
          this.$emit('shengHeListCustom', { scope: scope, status: 1 })
        }
      }).catch((value) => {
        if (value === 'cancel') {
          this.$emit('shengHeListCustom', { scope: scope, status: 2, details: value })
        }
      })
    }
  }
}
</script>

<style lang="scss" scoped>
</style>
